import { apiClient } from './apiClient';

const API_URL = process.env.NEXT_PUBLIC_API_URL;

export const adminService = {
  // ==================== AUTHENTICATION ====================
  
  /**
   * Google Login for Admin
   */
  googleLogin: async (idToken: string) => {
    try {
      const response = await apiClient.post<any>('/api/admin/google-login', { idToken });
      return { success: true, admin: response.admin };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Refresh admin token
   */
  refreshToken: async () => {
    try {
      const response = await apiClient.post('/api/admin/refresh');
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Logout admin
   */
  logout: async () => {
    try {
      await apiClient.post('/api/admin/logout');
      return { success: true };
    } catch (error: any) {
      console.error('Admin logout error:', error);
      throw error;
    }
  },

  /**
   * Get admin profile
   */
  getProfile: async () => {
    try {
      const response = await apiClient.get<any>('/api/admin/profile');
      return { success: true, admin: response.admin };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  // ==================== USER MANAGEMENT ====================

  /**
   * Get all users with pagination and search
   */
  getAllUsers: async (page = 1, limit = 10, search = "") => {
    try {
      const queryParams = new URLSearchParams({
        page: page.toString(),
        limit: limit.toString(),
        ...(search && { search }),
      });

      const response = await apiClient.get<any>(
        `/api/admin/alluser?${queryParams.toString()}`
      );
      return { success: true, data: response.data };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Get complete user data by ID
   */
  getUserCompleteData: async (userId: string) => {
    try {
      const response = await apiClient.get<any>(`/api/admin/user/${userId}`);
      return { success: true, data: response.data };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Export all data to Excel
   */
  exportAllData: async () => {
    try {
      const response = await fetch(`${API_URL}/api/admin/export`, {
        method: 'POST',
        credentials: 'include',
      });

      if (!response.ok) {
        throw new Error('Export failed');
      }

      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      
      const contentDisposition = response.headers.get('Content-Disposition');
      let filename = `astrology_data_${new Date().toISOString().split('T')[0]}.xlsx`;
      
      if (contentDisposition) {
        const filenameMatch = contentDisposition.match(/filename="?(.+)"?/);
        if (filenameMatch) {
          filename = filenameMatch[1];
        }
      }
      
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);

      return { success: true };
    } catch (error: any) {
      console.error('Export error:', error);
      return { success: false, error: error.message };
    }
  },

  // ==================== PLANET LOCATION PREDICTIONS ====================

  /**
   * Create new planet location prediction (Admin only)
   */
  createPlanetPrediction: async (data: {
    lagnaya: string;
    PredictionPlanetId: number;
    planetlocation: string;
    prediction: string;
  }) => {
    try {
      const response = await apiClient.post('/api/admin/planet-predictions', data);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Get all planet predictions with optional filters
   */
  getAllPlanetPredictions: async (filters?: {
    lagnaya?: string;
    planetId?: number;
    planetlocation?: string;
  }) => {
    try {
      const queryParams = new URLSearchParams();
      if (filters?.lagnaya) queryParams.append('lagnaya', filters.lagnaya);
      if (filters?.planetId) queryParams.append('planetId', filters.planetId.toString());
      if (filters?.planetlocation) queryParams.append('planetlocation', filters.planetlocation);

      const url = queryParams.toString() 
        ? `/api/admin/planet-predictions?${queryParams.toString()}`
        : '/api/admin/planet-predictions';

      const response = await apiClient.get<any>(url);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Search prediction by lagnaya, planet, and location
   */
  searchPlanetPrediction: async (
    lagnaya: string,
    planetId: number,
    planetlocation: string
  ) => {
    try {
      const queryParams = new URLSearchParams({
        lagnaya,
        planetId: planetId.toString(),
        planetlocation,
      });

      const response = await apiClient.get<any>(
        `/api/admin/planet-predictions/search?${queryParams.toString()}`
      );
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Get planet prediction by ID
   */
  getPlanetPredictionById: async (id: number) => {
    try {
      const response = await apiClient.get<any>(`/api/admin/planet-predictions/${id}`);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Update planet location prediction (Admin only)
   */
  updatePlanetPrediction: async (
    id: number,
    data: {
      lagnaya?: string;
      PredictionPlanetId?: number;
      planetlocation?: string;
      prediction?: string;
    }
  ) => {
    try {
      const response = await apiClient.put(`/api/admin/planet-predictions/${id}`, data);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Delete planet location prediction (Admin only)
   */
  deletePlanetPrediction: async (id: number) => {
    try {
      const response = await apiClient.delete(`/api/admin/planet-predictions/${id}`);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  // ==================== API KEY MANAGEMENT ====================

  /**
   * Create new API key (Admin only)
   */
  createApiKey: async (apiKey: string) => {
    try {
      const response = await apiClient.post('/api/admin/apikey', { apiKey });
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Get API key by ID (returns masked key)
   */
  getApiKey: async (apiKeyId: string) => {
    try {
      const response = await apiClient.get<any>(`/api/admin/apikey?apiKeyId=${apiKeyId}`);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  /**
   * Update API key (Admin only)
   */
  updateApiKey: async (apiKey: string, apikeyId: string) => {
    try {
      const response = await apiClient.put('/api/admin/apikey', { apiKey, apikeyId });
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },

  getIncomeAnalytics: async () => {
  try {
    const response = await apiClient.get<any>('/api/admin/income-analytics');
    return { success: true, analytics: response.analytics };
  } catch (error: any) {
    return { success: false, error: error.message };
  }
},
};

