"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { toast } from "react-toastify";
import { adminService } from "../services/adminService";
import { Shield, Loader2 } from "lucide-react";
import GoogleLoginButton from "../components/auth/GoogleLoginButton";

export default function AdminLogin() {
  const router = useRouter();
  const [loading, setLoading] = useState(false);

  const handleGoogleSuccess = async (idToken: string) => {
    setLoading(true);
    try {
      

      if (!idToken) {
        throw new Error("No credential received from Google");
      }

      const result = await adminService.googleLogin(idToken);

      if (result.success) {
        toast.success("Admin login successful!");
        router.push("/admin/dashboard");
      } else {
        throw new Error(result.error || "Login failed");
      }
    } catch (error: any) {
      console.error("Google login error:", error);
      toast.error(error.message || "Login failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleGoogleError = () => {
    toast.error("Google login failed. Please try again.");
  };

  return (
    <div className="min-h-screen bg-linear-to-br from-indigo-100 via-purple-50 to-pink-100 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/* Admin Badge */}
        <div className="flex justify-center mb-6">
          <div className="bg-white rounded-full p-4 shadow-lg">
            <Shield className="w-12 h-12 text-indigo-600" />
          </div>
        </div>

        {/* Login Card */}
        <div className="bg-white rounded-2xl shadow-2xl p-8 space-y-6">
          {/* Header */}
          <div className="text-center space-y-2">
            <h1 className="text-3xl font-bold text-gray-900">Admin Portal</h1>
            <p className="text-gray-600">
              Sign in with your authorized Google account
            </p>
          </div>

          {/* Divider */}
          <div className="relative">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-gray-200"></div>
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-4 bg-white text-gray-500">
                Secure Admin Access
              </span>
            </div>
          </div>

          {/* Google Login Button */}
          <div className="flex justify-center px-10">
            <GoogleLoginButton
              onSuccess={handleGoogleSuccess}
              onError={handleGoogleError}
              text={ "signin_with"}
            />
          </div>

          {/* Security Notice */}
          <div className="bg-indigo-50 border border-indigo-100 rounded-lg p-4">
            <div className="flex items-start space-x-3">
              <Shield className="w-5 h-5 text-indigo-600 mt-0.5 shrink-0" />
              <div className="text-sm text-indigo-800">
                <p className="font-medium mb-1">Security Notice</p>
                <p className="text-indigo-700">
                  Only authorized admin accounts can access this portal. Your
                  login attempt will be verified.
                </p>
              </div>
            </div>
          </div>

          {/* Footer */}
          <div className="text-center text-sm text-gray-500 pt-4 border-t border-gray-100">
            <p>Not an admin?</p>
            <button
              onClick={() => router.push("/auth")}
              className="text-indigo-600 hover:text-indigo-700 font-medium mt-1"
            >
              Go to user login â†’
            </button>
          </div>
        </div>

        {/* Additional Info */}
        <div className="text-center mt-6 text-sm text-gray-600">
          <p>Protected by enterprise-grade security</p>
        </div>
      </div>
    </div>
  );
}
